//Главный файл стилей.

body {
  background-color: #98fb98;
  display: flex;
  justify-content: center;
}

ul {
  list-style-type: none;
  margin: 0;
  margin-top: 10px;
  padding: 0;
}
h2,
h3 {
  margin: 0;
  padding: 0;
}

.container {
  margin-top: 100px;
}

.country-search {
  text-align: center;
}

.heading {
  font-size: 34px;
}

.country-card__name {
  text-align: center;
  font-size: 24px;
}

.country-search__input {
  display: block;
  margin: 10px auto;
  padding: 5px 20px;
  font-size: 16px;
  line-height: 1.5;
  border-radius: 5 px;
  color: #001d1a;
}

.country-card__general-info {
  display: flex;
  justify-content: space-between;
}

.country-card__flag {
  margin-left: 30px;
}

.country-card__info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.pnotify {
  position: absolute;
  transform: translateY(200%);
  height: 50px;
  background-color: red;
  text-align: center;
}
